import React from "react";
import { Typography } from "@material-ui/core";
import ProgressBar from "progressbar.js";
import ScrollAnimation from 'react-animate-on-scroll';

const animate = [
    "animate__fadeInDownBig",
    "animate__fadeInLeftBig",
    "animate__fadeInRightBig",
    "animate__fadeInUpBig",
    "animate__fadeInTopLeft",
    "animate__fadeInTopRight",
    "animate__fadeInBottomLeft",
    "animate__fadeInBottomRight"
]

class Skills extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            bar: null
        }
    }

    componentDidMount() {
        const container = `#skill-rating-${this.props.index}`

        var bar = new ProgressBar.Line(container, {
            strokeWidth: 2,
            duration: 1400,
            easing: 'easeInOut',
            color: '#B400B4',
            trailColor: '#6C0078',
            trailWidth: 1,
            svgStyle: {width: '100%', height: '100%'},
            from: {color: '#6C0078'},
            to: {color: '#B400B4'},
            text: {
                style: {
                    color: '#b74cf5',
                    position: 'absolute',
                    right: 0,
                    bottom: 0,
                    padding: 0,
                    margin: 0,
                    transform: null,
                }
            },
            step: (state, bar) => {
              bar.path.setAttribute('stroke', state.color);
              bar.setText(`${Math.round(bar.value() * 100)} %`);
            }
          });
          
          this.setState({ bar })
    }

    getScrollAnimation() {
        const randIndex = Math.floor(Math.random() * 8)
        return animate[randIndex]
    }

    animateProgress(visible) {
        if(this.state.bar === null) return
        
        if(visible.inViewport) {
            this.state.bar.animate((this.props.level / 100)); 
        }
        
    }

    render() {
        return <ScrollAnimation 
            animateIn={this.getScrollAnimation()} 
            duration={1} 
            animateOnce
            afterAnimatedIn={this.animateProgress.bind(this)}
        >
            <div id={`skill-rating-${this.props.index}`}
                style={{
                    fontFamily: "Titillium Web",
                    marginLeft: 20,
                    marginRight: 20,
                }}
            >
                <Typography variant="h1" color="secondary" align="center" >
                    <i className={this.props.icon} />
                </Typography>
                <Typography variant="body2" color="secondary" align="center">
                    {this.props.name}
                </Typography>
            </div>
        </ScrollAnimation>
    }
}

export default Skills