import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import ScrollAnimation from 'react-animate-on-scroll';

import Parallax from "./Parallax";
import Skill from "./Skill";
import techStyle from "../styles/technicals.module.css";
import image from "../images/mastery-bg.jpg";

const masteries = [
    { name: "Bitbucket", icon: "devicon-bitbucket-plain", level: 80 },
    { name: "Git", icon: "devicon-git-plain", level: 75 },
    { name: "Github", icon: "devicon-github-plain", level: 60 },
    { name: "Gitlab", icon: "devicon-gitlab-plain", level: 60 },
    { name: "PHP", icon: "devicon-php-plain", level: 70 },
    { name: "CodeIgniter", icon: "devicon-codeigniter-plain", level: 85 },
    { name: "Docker", icon: "devicon-docker-plain", level: 65 },
    { name: "Vagrant", icon: "devicon-vagrant-plain", level: 65 },
    { name: "Javascript", icon: "devicon-javascript-plain", level: 80 },
    { name: "Typescript", icon: "devicon-typescript-plain", level: 50 },
    { name: "NodeJS", icon: "devicon-nodejs-plain", level: 70 },
    { name: "ExpressJS", icon: "devicon-express-original", level: 70 },
    { name: "ReactJS", icon: "devicon-react-original", level: 60 },
    { name: "VueJS", icon: "devicon-vuejs-plain", level: 65 },
    { name: "MySQL", icon: "devicon-mysql-plain", level: 80 },
    { name: "PostgreSQL", icon: "devicon-postgresql-plain", level: 60 },
    { name: "Mongo DB", icon: "devicon-mongodb-plain", level: 60 },
    { name: "Sequelize", icon: "devicon-sequelize-plain", level: 70 },
    { name: "Ubuntu OS", icon: "devicon-ubuntu-plain", level: 75 },
    { name: "VisualStudio Code", icon: "devicon-visualstudio-plain", level: 60 }
]

export default function Mastery() {

    return <Parallax bg={image}>
        <Grid className={techStyle.parentGrid} container alignItems="center" alignContent="center">
            <Grid container item alignContent="center" justify="center">
                <ScrollAnimation animateIn="animate__zoomInDown" animateOnce>
                    <Typography variant="h2" color="primary" align="center" gutterBottom>
                        <Box fontWeight="fontWeightBold">
                            MASTERY
                        </Box>
                    </Typography>
                </ScrollAnimation>
            </Grid>

            <Grid
                container item
                alignItems="center" alignContent="center"
            >
                    {masteries.map((mastery, index) => {
                        return <Grid
                            key={`mastery-icon-grid-${index}`} 
                            container item 
                            sm={6} md={3} lg={4} 
                            style={{marginTop: 10, marginBottom: 10}}
                            alignItems="center" alignContent="center" justify="center"
                        >
                            <Skill name={mastery.name} level={mastery.level} icon={mastery.icon} index={index} />
                        </Grid>
                    })}
            </Grid>
        </Grid>
    </Parallax>
}